import "./App.css";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import HomePage from "./Components/HomePage/HomePage";

function App() {
  return (
    <BrowserRouter basename="/biokudi-frontend">
      <Routes>
        <Route path="/biokudi-frontend" element={<HomePage />} />
      </Routes>
    </BrowserRouter>
  );
}

export default App;
